@charset "UTF-8";
@font-face {
  font-family: "svg-icons";
  src: url("../../../../../fonts/svg-icons.eot");
  src: url("../../../../../fonts/svg-icons.?#iefix") format("embedded-opentype"), url("../../../../../fonts/svg-icons.woff") format("woff"), url("../../../../../fonts/svg-icons.ttf") format("truetype"), url("../../../../../fonts/svg-icons.#svg-icons") format("svg");
  font-weight: normal;
  font-style: normal; }

.icon {
  font-family: "svg-icons"; }
  .icon:before {
    /* Some common icon stuff */ }
  .icon.icon--dribbble:before {
    content: ""; }
  .icon.icon--facebook:before {
    content: ""; }
  .icon.icon--google-plus:before {
    content: ""; }
  .icon.icon--linkedin:before {
    content: ""; }
  .icon.icon--twitter:before {
    content: ""; }
  .icon.icon--vimeo:before {
    content: ""; }

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  font-family: "Cabin", Arial, sans-serif; }

.c-footer {
  display: flex;
  justify-content: space-between;
  height: 168px;
  background-color: #f2f5f8; }
  @media only screen and (max-width: 375px) {
    .c-footer {
      flex-direction: column;
      align-items: center;
      height: 370px; } }
  @media only screen and (min-width: 376px) and (max-width: 768px) {
    .c-footer {
      flex-direction: column;
      align-items: center;
      height: 325px; } }
  .c-footer .c-logo-img {
    text-align: center; }
  .c-footer__logo-menu {
    margin-top: 50px;
    margin-left: 50px; }
    @media only screen and (max-width: 375px) {
      .c-footer__logo-menu {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-left: 0; } }
    @media only screen and (min-width: 376px) and (max-width: 768px) {
      .c-footer__logo-menu {
        margin-left: 0; } }
  .c-footer__menu {
    display: inline-block;
    margin-top: 20px;
    margin-left: 50px;
    vertical-align: top; }
    @media only screen and (max-width: 375px) {
      .c-footer__menu {
        margin-left: 0; } }
    @media only screen and (min-width: 376px) and (max-width: 768px) {
      .c-footer__menu {
        margin-left: 30px; } }
    .c-footer__menu-item {
      color: #383737;
      font-size: 16px;
      line-height: 2.25;
      letter-spacing: 0.02rem;
      text-decoration: none; }
      @media only screen and (max-width: 375px) {
        .c-footer__menu-item {
          margin-right: 10px; } }
      @media only screen and (min-width: 376px) and (max-width: 768px) {
        .c-footer__menu-item {
          margin-right: 20px; } }
      @media only screen and (min-width: 769px) and (max-width: 1024px) {
        .c-footer__menu-item {
          margin-right: 20px; } }
      @media only screen and (min-width: 1025px) {
        .c-footer__menu-item {
          margin-right: 40px; } }
      .c-footer__menu-item:hover {
        text-decoration: underline; }
  .c-footer .c-social {
    display: inline-block;
    margin-top: 20px;
    text-align: center;
    vertical-align: top; }
    @media only screen and (min-width: 376px) and (max-width: 768px) {
      .c-footer .c-social {
        display: block; } }
    .c-footer .c-social-item {
      display: inline-block;
      width: 36px;
      height: 36px;
      padding: 10px;
      border: 1px solid transparent;
      background-color: transparent;
      outline: none;
      color: #383737;
      font-size: 14px;
      text-decoration: none; }
      .c-footer .c-social-item:hover {
        border-color: #383737;
        border-radius: 999rem;
        cursor: pointer; }
  .c-footer__address {
    margin-top: 40px;
    margin-right: 120px;
    font-size: 16px;
    letter-spacing: 0.02rem; }
    @media only screen and (max-width: 375px) {
      .c-footer__address {
        margin-top: 0;
        margin-right: 0;
        padding-bottom: 30px; } }
    @media only screen and (min-width: 376px) and (max-width: 768px) {
      .c-footer__address {
        margin-right: 0;
        padding-bottom: 30px; } }
    .c-footer__address-city {
      display: block;
      color: #383737;
      line-height: 2.25; }
    .c-footer__address-street {
      display: block;
      color: #b4bbc2;
      line-height: 1.25; }
    .c-footer__address-tel {
      display: block;
      color: #b4bbc2;
      line-height: 1.25;
      text-decoration: none;
      cursor: default; }
